.model-content-header {
    width: 100%;
    height: 380px;
    background-color: var(--primary-dark);
    border-radius: 4px;
    position: relative;
    overflow: hidden;
}

.model-content-header-logo {
    width: 270px;
    height: 340px;
    border-radius: 10px;
    background-position: 50%;
    background-size: cover;
    border: 1px solid var(--white);
    padding: 14px;
    box-sizing: border-box;
    text-align: right;
}

.model-content-header-info {
    width: 100%;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    position: relative;
    background: linear-gradient(to right, var(--primary-dark) 0%, var(--primary-dark) 40%, rgba(0, 0, 0, 0) 80%);
}

.model-content-header-info > div:nth-child(1) {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.model-content-header-info h1 {
    font-size: 56px;
    font-weight: 500;
    margin: 0;
    padding: 0;
    margin-bottom: 30px;
}

.model-content-header-stats {
    display: flex;
    flex-direction: row;
}

.model-content .tube-chip, .home-content .model-content-header .tube-chip {
    background-color: var(--primary-light);
    color: var(--white);
    font-size: 14px;
    width: auto;
    margin-right: 3px;
}

.model-content .tube-chip svg, .home-content .model-content-header .tube-chip svg {
    font-size: 20px;
    margin-top: 2px;
    margin-left: 10px;
    margin-right: 3px;
}

.model-content .tube-chip svg:nth-child(1), .home-content .model-content-header .tube-chip svg:nth-child(1) {
    margin-left: 0;
}

.model-content .tube-chip:hover, .home-content .model-content-header .tube-chip:hover {
    background-color: var(--primary);
}

.model-content-header-shortdesc {
    font-size: 14px;
    margin: 15px 0;
}

.model-content-header-rc {
    padding-left: 25px;
}

.model-content-header-models {
    display: flex;
    flex-direction: row;
    font-size: 14px;
    font-weight: 400;
    align-items: center;
}

.model-content-header-model {
    width: 34px;
    height: 34px;
    margin-left: 10px;
    background-position: 50%;
    background-size: cover;
    border-radius: 100%;
    border: 1px solid var(--primary-light)
}

.model-content-header-media {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.model-content-header-media-item {
    background-color: var(--primary);
    height: 68px;
    width: calc((100% - 40px) / 5);
    border-radius: 5px;
}

.model-content-header-background {
    width: 60%;
    height: 100%;
    background-position: 50%;
    background-size: cover;
    position: absolute;
    right: 0;
    top: 0;
}

.model-content-subscribe {
    padding-top: 20px; 
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.model-content-subscribe .model-content-subscribe-lc .confirm-button {
    margin-right: 10px;
    color: var(--button);
}

.model-content-subscribe .model-content-subscribe-rc .confirm-button {
    top: 0;
    margin-left: 10px;
}

.model-content-subscribe .model-content-subscribe-lc .confirm-button:last-child {
    color: var(--link);
}

.model-content-about > a:hover video {
    display: block;
}

.model-content-about {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.model-content-about > a,
.model-content-about > div {
    width: calc((100% - 70px) / 4);
    font-size: 14px;
}

.model-content-about > div.about-me {
    width: calc((100% - 70px) / 2);
    padding-bottom: 1150px;
}

.model-content-about .categories-content-tags {
    margin-top: 20px;
}

.model-content h2 {
    margin-top: 20px;
    margin-bottom: 30px;
    padding: 0;
    font-weight: 500;
    font-size: 24px;
}

.model-content-about .model-content-about-titles {
    width: 100%;
    display: flex;
}

.model-content-about .model-content-about-titles h2:nth-child(1) {
    width: calc((100% - 40px) / 3 * 2 + 40px);
}

.model-content .categories-content-videos-video {
    height: 188px;
}

.model-content-about .categories-content-videos-video {
    height: 255px;
}

.model-content .categories-content-albums-album, .model-content .categories-content-models-model {
    height: 340px;
}

.model-content .categories-content-albums, .model-content .categories-content-models, .model-content .categories-content-videos {
    padding-top: 0;
}

.model-content .line {
    margin-bottom: 10px;
    margin-top: 15px;
}

.model-content-about-stats {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: flex-start
}

.model-content-about-stats div {
    width: 50%;
    height: 24px;
}

.model-content-about-stats div:nth-child(2n+1) {
    font-weight: 500;
}

.model-content-about-stats div:nth-child(2n) {
    font-weight: 500;
    text-align: right;
}

.model-content-about-stats div:nth-child(2n) span {
    background: var(--primary-light);
    padding: 0px 10px;
    display: inline-block;
    border-radius: 10px;
    box-sizing: border-box;
    font-size: 12px;
}

.model-flag {
    width: 38px;
    border-radius: 5px;
}

.model-social a {
    width: 36px !important;
    height: 36px !important;
    margin-left: 5px;
    color: var(--button);
}

.model-social {
    position: absolute;
    right: 15px;
    bottom: 20px;
}

.model-content-about > div.model-detail-featured-videos {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: var(--primary-dark);
    padding: 30px calc((100% - 1440px) / 2);
    margin-top: 320px;
    gap: 20px;
    flex-wrap: wrap;
    position: absolute;
    left: 0;
    box-sizing: border-box;
}

.model-detail-featured-videos > a {
    width: calc((100% - 80px) / 5);
}

.model-detail-featured-videos > a > .categories-content-videos-video {
    height: 155px;
}

.model-detail-featured-videos > h2 {
    width: 100%;
    font-size: 18px;
    padding: 0px;
    margin-bottom: 10px;
    margin-top: 0px;
}

.model-detail-related-models > a {
    width: calc((100% - 120px) / 7);
}

.model-detail-related-models .categories-content-models-model {
    background-color: var(--primary);
}

.model-detail-featured-videos .categories-content-videos-video {
    background-color: var(--primary);
}

.model-detail-related-models {
    display: flex;
    flex-direction: row;
    gap: 20px;
    width: 100%;
}

.model-detail-featured-videos > h2.related-models-title {
    padding-top: 50px;
}

.social-icon.custom-social-icon {
    display: inline-block;
    position: relative;
    overflow: hidden;
    vertical-align: middle;
}

.social-icon.custom-social-icon span {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background-color: var(--button);
}

.social-icon.custom-social-icon span svg {
    width: 100%;
    height: 100%;
}

.social-icon.custom-social-icon span svg g {
    fill: var(--white);
}