.album-wrapper {
    display: flex;
    flex-direction: row;
}

.album-content {
    flex: 1;
    padding-right: 30px;
    box-sizing: border-box;
}

.album-content-album {
    width: 100%;
    height: 580px;
    background-color: var(--primary-dark);
    position: relative;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: contain;
    border: 20px solid var(--primary-dark);
    box-sizing: border-box;
    border-radius: 15px;
}

.album-content-album-div, .album-content-album iframe {
    width: 100%;
    height: 100%;
}

.album-content h1 {
    font-size: 24px;
    font-weight: 500;
    margin-top: 30px;
}

.album-content-description {
    font-size: 14px;
    margin-top: 30px;
}

.album-content-interactions {
    padding-top: 0px;
    padding-bottom: 10px;
    display: flex;
    flex-direction: row;
}

.album-content-interactions-info {
    font-size: 14px;
    display: flex;
    align-items: center;
}

.album-content-interactions-info svg {
    font-size: 18px;
    margin-right: 4px;
}

.album-content-interactions-info svg:not(:first-child){
    margin-left: 15px;
}

.album-content-interactions-buttons {
    display: flex;
    flex-direction: row;
    flex: 1;
    justify-content: right;
}

.album-content-interactions-buttons .confirm-button.plainOutlined {
    color: var(--button);
    margin-left: 10px;
}

.album-content-interactions-buttons .confirm-button.contained {
    margin-left: 10px;
    top: 0;
}

.album-content-user, .album-content-models, .album-content-categories {
    display: flex;
    flex-direction: row;
    padding-top: 0px;
    padding-bottom: 10px;
    align-items: center;
    flex-wrap: wrap;
}

.album-content-user-avatar {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
    background-color: rgb(50, 66, 83);
}

.album-content-user .confirm-button {
    top: 0;
}

.album-content-user-name {
    flex: 1;
    padding-left: 15px;
    font-weight: 700;
}

.album-content-models h2, .album-content-categories h2 {
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    padding: 0;
    margin-bottom: 10px;
    width: 100%;
}

.album-content-model-avatar {
    width: 34px;
    height: 34px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
    margin-right: 10px;
    background-color: rgb(50, 66, 83);
}

.album-content-model-name {
    margin-right: 20px;
}

.album-content-album  .confirm-button.plainOutlined {
    color: var(--button);
    min-width: 0;  
    position: absolute;
    top: 50%;
    margin-top: -16px;
}

.album-content-album  .confirm-button.plainOutlined:nth-child(2) {
    right: 0px;
}

.album-content-thumbnails-photo {
    width: calc((100% - 120px) / 7);
    height: 120px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    box-sizing: border-box;
    border: 1px solid var(--primary-dark);
    background-color: var(--primary-dark);
    border-radius: 15px;
    cursor: pointer;
    margin-bottom: 20px;
    margin-left: 20px;
}

.album-content-thumbnails-photo.active {
    border: 1px solid var(--buttons);
}

.album-content-thumbnails {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 25px;
}

.album-content-thumbnails-photo:nth-child(7n + 1) {
    margin-left: 0px;
}

.album-content .show-more {
    position: relative;
    top: -5px;
}