.header > div {
    width: 1440px;
    display: flex;
    align-items: center;
    position: relative;
}

.header {
    width: 100%;
    display: flex;
    justify-content: center;
}

.header > div > .header-search {
    flex: 1;
}

.header > div > .text-input {
    margin: 0 75px
}

.header > div > .user-menu {
    display: flex;
    align-items: center;
}

.header > div > .confirm-button {
    white-space: nowrap;
}

.header .search-icon {
    position: absolute;
    color: var(--black);
    right: 150px;
    opacity: 0.2;
}

.header .text-input-wrapper {
    align-items: center;
}

.header .text-input-wrapper > div{
    max-width: 900px;
    width: 100%;
}
