.categories-content-models {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 25px
}

.categories-content-models > a {
    width: calc((100% - 90px) / 6);
    display: flex;
    flex-direction: column;
    margin-bottom: 18px;
    margin-left: 18px
}

.categories-content-models > a:nth-child(6n+1) {
    margin-left: 0px
}

.categories-content-models-model-thumb {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: 50%;
    transition: width 0.3s, height 0.3s, margin-left 0.3s, margin-top 0.3s;
}

.categories-content-models-model {
    width: 100%;
    height: 260px;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
    background-color: var(--primary-dark);
}

.categories-content-models > a:hover .categories-content-models-model-thumb {
    width: 110%;
    height: 110%;
    margin-left: -5%;
    margin-top: -5%;
}

.categories-content-models-model-media {
    position: absolute;
    bottom: 28px;
    right: 5px;
    color: var(--white);
    font-size: 12px;
    background-color: rgba(0,0,0,0.6);
    padding: 0 6px
}

.categories-content-models-model-media svg {
    font-size: 14px;
    position: relative;
    top: 2px;
}

.categories-content-models-model-media:nth-child(2) {
    bottom: 5px;
}

.categories-content-models-name {
    color: var(--white);
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 5px;
    opacity: 0.7;
}

.categories-content-models-info {
    color: var(--white);
    opacity: 0.4;
    font-size: 12px;
}

.categories-content-models-info svg {
    font-size: 12px;
    position: relative;
    top: 2px;
    margin-left: 7px;
}

.categories-content-models-info svg:nth-child(1){
    margin-left: 0;
}

.categories-content-models > a:hover .categories-content-models-info {
    opacity: 0.5;
}

.categories-content-models > a:hover .categories-content-models-name {
    opacity: 1;
}