.title-14 {
    font-size: 14px;
    color: var(--black);
}

.mt-10 {
    margin-top: 10px;
}

.mt-20 {
    margin-top: 20px;
}

.mt-30 {
    margin-top: 30px;
}

.mb-5 {
    margin-bottom: 5px;
}

.mb-10 {
    margin-bottom: 10px;
}

.mr-5 {
    margin-right: 5px;
}

.MuiCircularProgress-root svg {
    color: var(--button);
}

.MuiLinearProgress-root {
    height: 10px !important;
    background-color: var(--primary-dark);
}

.MuiLinearProgress-root span {
    background-color: var(--button) !important;
}

.login-window {
    width: 500px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border: 1px solid var(--link);
    padding: 20px;
    border-radius: 10px;
    margin-top: 30px;
}

.login-window .tube-btn {
    text-align: center;
    padding: 10px;
    font-size: 16px;
}

.login-window .incorrect-password {
    color: var(--error);
    text-align: center;
    margin-top: 10px;
}

* {
    scrollbar-width: thin;
    scrollbar-color: var(--primary-light) transparent;
  }

.content {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    padding-top: 30px
}

.content.content-embed {
    padding: 0;
    margin: 0;
    max-width: 100%;
    overflow: hidden;
}

a {
    text-decoration: none;
}

.line {
    width: 100%;
    height: 1px;
    background-color: var(--primary-light);
    margin-top: 10px;
    margin-bottom: 20px;
}

.out-of-service {
    width: 500px;
    margin: 0 auto;
    text-align: center;
}

.MuiTooltip-popper .MuiTooltip-tooltip {
    background-color: var(--primary);
    border: 1px solid var(--black);
    color: var(--white);
}