.channel-content-header {
    width: 100%;
    height: 440px;
    background-color: var(--primary-dark);
    border-radius: 4px;
    position: relative;
}

.channel-content-header-logo {
    width: 138px;
    height: 138px;
    border-radius: 100%;
    background-position: 50%;
    background-size: cover;
}

.channel-content-header-info {
    width: 100%;
    box-sizing: border-box;
    padding: 50px;
    padding-right: 50%;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    background: linear-gradient(to right, var(--primary-dark) 0%, var(--primary-dark) 40%, rgba(0, 0, 0, 0) 80%);
    position: relative;
}

.channel-content-header-info > div:nth-child(1) {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.channel-content-header-info h1 {
    font-size: 28px;
    font-weight: 500;
    margin: 0;
    padding: 0;
}

.channel-content-header-stats {
    display: flex;
    flex-direction: row;
}

.channel-content .tube-chip {
    background-color: var(--primary-light);
    color: var(--white);
    font-size: 14px;
    width: auto;
    margin-right: 3px;
}

.channel-content .tube-chip svg {
    font-size: 20px;
    margin-top: 2px;
    margin-left: 10px;
    margin-right: 3px;
}

.channel-content .tube-chip svg:nth-child(1) {
    margin-left: 0;
}

.channel-content .tube-chip:hover {
    background-color: var(--primary);
}

.channel-content-header-shortdesc {
    font-size: 14px;
    margin: 15px 0;
}

.channel-content-header-rc {
    padding-left: 25px;
}

.channel-content-header-models {
    display: flex;
    flex-direction: row;
    font-size: 14px;
    font-weight: 400;
    align-items: center;
}

.channel-content-header-model {
    width: 34px;
    height: 34px;
    margin-left: 10px;
    background-position: 50%;
    background-size: cover;
    border-radius: 100%;
    border: 1px solid rgba(255, 255, 255, .5)
}

.channel-content-header-media {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: start;
}

.channel-content-header-media-item {
    background-color: var(--primary);
    height: 68px;
    width: calc((100% - 40px) / 5);
    border-radius: 5px;
}

.channel-content-header-background {
    width: 60%;
    height: 100%;
    background-position: 50%;
    background-size: cover;
    position: absolute;
    right: 0;
    top: 0;
}

.channel-content-subscribe {
    background-color: var(--primary-light);
    background-image: linear-gradient(to right, var(--primary-dark) 20%, var(--primary) 60%);
    padding: 45px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 4px;
}

.channel-content-subscribe .channel-content-subscribe-lc .confirm-button {
    top: 0;
    margin-right: 10px;
}

.channel-content-subscribe .channel-content-subscribe-rc .confirm-button {
    margin-left: 10px;
    color: var(--button);
}

.channel-content-about > a:hover video {
    display: block;
}

.channel-content-about {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.channel-content-about > div {
    width: calc((100% - 40px) / 3);
    font-size: 14px;
}

.channel-content-about > div.channel-content-featured {
    width: calc(((100% - 40px) / 3) * 2);
    font-size: 14px;
}

.channel-content-about > div.channel-content-featured > a {
    width: calc((100% - 20px) / 2);
    font-size: 14px;
}

.channel-content-about > div.channel-content-featured > a:first-child {
    margin-right: 20px;
}

.channel-content-about .categories-content-tags {
    margin-top: 20px;
}

.channel-content h2 {
    margin-top: 20px;
    margin-bottom: 30px;
    padding: 0;
    font-weight: 500;
    font-size: 24px;
}

.channel-content-about .channel-content-about-titles {
    width: 100%;
    display: flex;
}

.channel-content-about .channel-content-about-titles h2:nth-child(1) {
    width: calc((100% - 40px) / 3 * 2 + 40px);
}

.channel-content .categories-content-videos-video {
    height: 188px;
}

.channel-content-about .categories-content-videos-video {
    height: 255px;
}

.channel-content .categories-content-albums-album, .channel-content .categories-content-models-model {
    height: 340px;
}

.channel-content .categories-content-albums, .channel-content .categories-content-models, .channel-content .categories-content-videos {
    padding-top: 0;
}

.channel-content-header-media a {
    width: calc((100% / 4) - 40px);
    height: 80px;
}

.channel-content-header-media a:not(:first-child) {
    margin-left: 10px;
}

.channel-content-header-media a .categories-content-videos-video {
    height: 100%;
    background-color: var(--primary);
}

.channel-content-header-media a:hover video {
    display: block;
}

.channel-content-featured {
    display: flex;
}

.channel-content-header-title-wrapper {
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
}

.channel-content-header-title-wrapper .confirm-button {
    top: 0px;
}