.confirm-button {
    color: var(--white);
    cursor: pointer;
    background-color: var(--button) !important;
    border: 1px solid var(--button) !important;
    border-radius: 30px !important;
    align-items: center;
    padding: 4px 20px !important;
    text-decoration: none;
    transition: all .2s;
    display: flex;     
    text-transform: none !important;
    font-weight: 300 !important;
    box-shadow: none !important;
    height: 32px;
    position: relative;
    top: 6px
}

.confirm-button:hover {
    background-color: var(--primary-dark) !important;
    border: 1px solid var(--primary-dark) !important;
    box-shadow: none !important;
}

.confirm-button.outlined {
    color: var(--white);
    background-color: transparent !important;
    border: 1px solid var(--white) !important;
}

.confirm-button.plain {
    color: var(--link);
    background-color: transparent !important;
    border: none !important;
    top: 0
}

.confirm-button.plainOutlined {
    color: var(--white);
    background-color: transparent !important;
    border: 1px solid var(--white) !important;
    top: 0
}

.confirm-button.plain img {
    margin-right: 7px;
}

.confirm-button.outlined:hover {
    border: 1px solid var(--primary-dark) !important;
    background-color: var(--primary-dark) !important;
}

.confirm-button.filter-button {
    background-color: var(--link) !important;
    border: 1px solid var(--link) !important;
    float: right
}

.tube-submit-button-icon {
    margin-right: 4px;
}

.confirm-button.no-label {
    margin-left: 0px !important;
    width: 140px    
}
.confirm-button.outlined.upload-button {
    border-color: var(--link) !important;
    white-space: nowrap;
}

.confirm-button.no-margin-top {
    top: 0
}

.confirm-button.with-input {
    top: 0px;  
    margin-bottom: 7px;    
}