.pagination-component > a > button {
    width: 36px;
    height: 36px;
    display: flex;
    background-color: var(--button);
    border-radius: 100%;
    justify-content: center;
    align-items: center;
    margin: 0 3px;
    font-size: 14px;
    color: var(--white);
}

.pagination-component > span {
    margin: 0 10px;
}

.pagination-component {
    padding: 15px 0;
}

.pagination-component .page-button-active,
.pagination-component > a > button:hover {
    background-color: var(--white);
    color: var(--button);
}