.text-area {
    width: 100%;
    margin-bottom: 7px !important;
}

.text-area textarea {
    border-radius: 7px;
    margin-bottom: 0;
    padding: 12px 11px;
    color: var(--black);
    font-weight: 300;
    font-size: 14px;
    resize: vertical;
    background-color: var(--white);
}

.text-area fieldset {
    border-color: transparent !important;
}

.text-area label {
    color: var(--primary) !important;
    line-height: 16px;
    font-size: 14px;
    margin-top: -2px;
}

.text-area label[data-shrink="true"] {
    line-height: 24px;
    font-size: 16px;
}

.text-area .MuiOutlinedInput-root {
    padding: 0;
}