.categories-content-channels {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 25px
}

.categories-content-channels > a {
    width: calc((100% - 54px) / 4);
    display: flex;
    flex-direction: column;
    margin-bottom: 18px;
    margin-left: 18px;
}

.categories-content-channels > a:nth-child(4n+1) {
    margin-left: 0px;
}

.categories-content-channels-channel-thumb {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: 50%;
    transition: width 0.3s, height 0.3s, margin-left 0.3s, margin-top 0.3s;
}

.categories-content-channels-channel {
    width: 100%;
    height: 150px;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
    background-color: var(--primary-dark);
}

.categories-content-channels > a:hover .categories-content-channels-channel-thumb {
    width: 110%;
    height: 110%;
    margin-left: -5%;
    margin-top: -5%;
}

.categories-content-channels-channel-media {
    position: absolute;
    bottom: 5px;
    right: 5px;
    color: var(--white);
    font-size: 12px;
    background-color: rgba(0,0,0,0.6);
    padding: 0 6px
}

.categories-content-channels-channel-media svg {
    font-size: 14px;
    position: relative;
    top: 2px;
}

.categories-content-channels-name {
    color: var(--white);
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: 5px;
    opacity: 0.7;
}

.categories-content-channels-info, .categories-content-users-info {
    color: var(--white);
    opacity: 0.4;
    font-size: 12px;
    position: absolute;
    top: 5px;
    right: 5px;
}

.categories-content-channels-info span, .categories-content-users-info span {
    padding: 3px;
    background: var(--black);
    margin-left: 3px;
    border-radius: 4px;
}

.categories-content-channels-info svg, .categories-content-users-info svg {
    font-size: 12px;
    position: relative;
    top: 2px;
    margin-left: 7px;
}

.categories-content-channels-info svg:nth-child(1){
    margin-left: 0;
}

a:hover .categories-content-channels-info, a:hover .categories-content-users-info {
    opacity: 0.7;
}

.categories-sidebar .categories-content-channels-info {
    position: relative;
    top: 0px;
    margin-top: -10px;
    margin-bottom: 5px;
    right: 0;
}

.categories-sidebar .categories-content-channels-info span {
    padding: 0px;
    background: transparent;
    margin-right: 6px;
    margin-left: 0px;
    border-radius: 0px;
}

.categories-sidebar-profile-name .categories-content-channels-info {
   margin-top: 0px; 
}

.categories-content-channels > a:hover .categories-content-channels-name {
    opacity: 1;
}

.categories-content-channels-channel-logo {
    width: 60px;
    height: 60px;
    position: absolute;
    bottom: 10px;
    left: 10px;
    border-radius: 100%;
    background-position: 50%;
    background-size: cover;
    border: 1px solid var(--primary-light);
    background-color: var(--primary);
}

.categories-content-channels-channel .confirm-button.outlined {
    position: absolute;
    top: 108px;
    right: 10px;
    font-weight: 500 !important;
}

.categories-content-channels-channel .confirm-button.outlined::after {
    content: "+";
    font-size: 26px;
    font-weight: 300;
    margin-top: -3px;
    margin-left: 5px;
}