.tube-player {
    position: relative;
    width: auto;
    height: 100%;
    background-size: cover;
    background-position: 50%;
}

.video-content-video.vertical .tube-player {
    background-size: contain;
    background-repeat: no-repeat;
}

.tube-player-controls {
    position: absolute;
    bottom: 0px;
    padding: 20px;
    box-sizing: border-box;
    height: 74px;
    width: 100%;
    display: flex;
}

.tube-player-controls-wrapper, .tube-player-play-stop-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tube-player-controls {
    position: absolute;
    bottom: 0px;
    padding: 20px;
    box-sizing: border-box;
    height: 74px;
    width: 100%;
    display: flex;
}

.tube-player-play {
    width: 50px;
    height: 100%;
    background-color: var(--primary-light);
    border-radius: 100px;
    justify-content: center;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    margin-right: 20px;
}

.tube-player-play-big {
    width: 80px;
    height: 50px;
    background-color: var(--primary-light);
    border-radius: 100px;
    justify-content: center;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    margin-right: 20px;
}

.tube-player-play:hover, .tube-player-play-stop-wrapper:hover .tube-player-play-big {
    background-color: var(--white);
}

.tube-player-play svg, .tube-player-play-big svg {
    color: transparent;
    font-weight: 300;
    font-size: 32px;
}

.tube-player-play svg path, .tube-player-play-big svg path {
    fill: none;
    stroke: var(--primary-dark);
    stroke-width: 1px;
    stroke-linejoin: round;
}

.tube-player-lc {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.tube-player-timeline {
    width: 100%;
    margin-top: -10px;
}

.tube-player-volume {
    width: 100px !important;
    margin-top: -9px;
    margin-left: 20px;
}

.tube-player-volume.vertical {
    width: 45px !important;
    margin-left: 0px;
}

.tube-player-timeline .MuiSlider-thumb, .tube-player-timeline .MuiSlider-track {
    color: var(--button);
}

.tube-player-timeline .Mui-focusVisible {
    box-shadow: none !important;
}

.tube-player-timeline .MuiSlider-thumb:hover, .tube-player-timeline .MuiSlider-thumb:active {
    box-shadow: 0px 0px 0px 8px rgba(255, 100, 0, 0.19) !important;
}


.tube-player-timeline .MuiSlider-rail {
    color: var(--white);
}

.tube-player-volume .MuiSlider-thumb, .tube-player-volume .MuiSlider-track {
    color: var(--white);
}

.tube-player-volume .Mui-focusVisible {
    box-shadow: none !important;
}

.tube-player-volume .MuiSlider-thumb:hover, .tube-player-volume .MuiSlider-thumb:active {
    box-shadow: 0px 0px 0px 8px rgba(255, 255, 255, 0.19) !important;
}


.tube-player-volume .MuiSlider-rail {
    color: var(--white);
    opacity: 0.3;
}

.tube-player-time {
    font-size: 12px;
    margin-top: -5px;
    width: 120px;
}

.tube-player-buttons {
    text-align: right;
    flex: 1;
}

.tube-player-buttons svg {
    font-size: 20px;
    margin-top: -10px;
    margin-left: 5px;
    cursor: pointer;
}

.tube-player video {
    object-fit: fill;
}

.tube-player-settings {
    position: absolute;
    right: 45px;
    bottom: 45px;
    background: var(--primary-dark);
}

.tube-player-settings > div {
    display: flex;
    cursor: pointer;
}

.tube-player-settings > div > div {
    margin: 5px;
    font-size: 14px;
}

.tube-player-settings > div > div:nth-child(2) {
    font-weight: 600;
}

.tube-player-pause-ads {
    width: 660px;
    display: flex;
    flex-direction: row;
    gap: 20px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -330px;
    margin-top: -120px;
    z-index: 2;
    justify-content: center;
}

.tube-player-ad-1,
.tube-player-ad-2 {
    overflow: hidden;
    width: 320px;
    height: 240px;
    position: absolute;
    left: 50%;
    margin-left: -330px;
    top: 50%;
    margin-top: -120px;
    z-index: 2;
}

.tube-player-ad-2 {
    margin-left: 10px;
}

.tube-player-ad-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
}

.tube-player-ad-container .confirm-button {
    position: absolute;
    bottom: 40px;
    right: 40px;
    top: auto;
}

.tube-player-ad-container .confirm-button.outlined {
    background-color: var(--primary-light) !important;
    border-color: var(--primary-light) !important;
}