.text-input {
    height: 36px;
}

.text-input input {
    border-radius: 7px;
    padding: 7px 11px;
    color: #333;
    font-weight: 300;
    font-size: 14px; 
    background-color: var(--white);
}

.text-input fieldset {
    border-radius: 7px;
    border-color: transparent !important;
}

.text-input .MuiInputBase-root {
    background-color: var(--white);
    border-color: var(--link) !important;
}

.text-input .MuiFormHelperText-root {
    color: var(--white)
}

.text-input label {
    color: var(--black) !important;
    line-height: 16px;
    font-size: 14px;
    margin-top: -2px;
}

.text-input label[data-shrink="true"] {
    line-height: 24px;
    font-size: 16px;
}