.tube-btn:hover:not(.tube-btn-active) {
    background-color: var(--primary-light);
}

.tube-btn {
    color: var(--primary);
    cursor: pointer;
    background-color: var(--white);
    border: 1px solid var(--link);
    border-radius: 7px;
    align-items: center;
    margin-bottom: 4px;
    margin-right: 4px;
    padding: 5px 7px;
    font-weight: 400;
    line-height: 1;
    text-decoration: none;
    transition: all .2s;
    display: inline-block;
    font-size: 14px;
}

.tube-btn-active {
    background-color: var(--primary-dark);
    border: 1px solid var(--primary-dark);
    color: var(--white);
}

.tube-btn-image img {
    width: 120px;
}

.tube-btn-image img {
    width: 120px;
    width: 120px;
    height: 28px;
    margin-bottom: 10px;
}

.tube-btn-image {
    background-color: var(--white);
    color: var(--black);
    display: inline-flex;
    flex-direction: column;
    padding: 15px;
    font-size: 16px;
    border-radius: 14px;
    border: 1px solid var(--link);
}

.tube-btn-active.tube-btn-image, .tube-btn-image:hover {
    border: 1px solid var(--button);
}

.tube-btn-active.button-switch-color-green {
    background-color: #66ffbf;
    border-color: #66ffbf;
    color: var(--primary-dark);
}

.tube-btn-active.button-switch-color-red {
    background-color: #ffafb1;
    border-color: #ffafb1;
    color: #c80000;
}