.user-content-header {
    width: 100%;
    height: auto;
    background-color: var(--primary-dark);
    border-radius: 4px;
    position: relative;
    overflow: hidden;
}

.user-content-header-logo {
    width: 138px;
    height: 138px;
    border-radius: 100%;
    background-position: 50%;
    background-size: cover;
    border: 1px solid var(--white);
}

.user-content-header-info {
    width: 100%;
    box-sizing: border-box;
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    position: relative;
}

.user-content-header-info > div:nth-child(1) {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.user-content-header-info h1 {
    font-size: 28px;
    font-weight: 500;
    margin: 0;
    padding: 0;
}

.user-content-header-stats {
    display: flex;
    flex-direction: row;
}

.user-content .tube-chip, .home-content .user-content-header .tube-chip {
    background-color: var(--primary-light);
    color: var(--white);
    font-size: 14px;
    width: auto;
    margin-right: 3px;
}

.user-content .tube-chip svg, .home-content .user-content-header .tube-chip svg {
    font-size: 20px;
    margin-top: 2px;
    margin-left: 10px;
    margin-right: 3px;
}

.user-content .tube-chip svg:nth-child(1), .home-content .user-content-header .tube-chip svg:nth-child(1) {
    margin-left: 0;
}

.user-content .tube-chip:hover, .home-content .user-content-header .tube-chip:hover {
    background-color: var(--primary-dark);
}

.user-content-header-shortdesc {
    font-size: 14px;
    margin: 15px 0;
}

.user-content-header-rc {
    padding-left: 25px;
}

.user-content-header-models {
    display: flex;
    flex-direction: row;
    font-size: 14px;
    font-weight: 400;
    align-items: center;
}

.user-content-header-model {
    width: 34px;
    height: 34px;
    margin-left: 10px;
    background-position: 50%;
    background-size: cover;
    border-radius: 100%;
    border: var(--primary-light);
}

.user-content-header-media {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.user-content-header-media-item {
    background-color: var(--primary-dark);
    height: 68px;
    width: calc((100% - 40px) / 5);
    border-radius: 5px;
}

.user-content-header-background {
    width: 100%;
    height: 100%;
    background-position: 50%;
    background-size: cover;
    position: absolute;
    right: 0;
    top: 0;
}

.user-content-subscribe {
    padding-top: 20px; 
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.user-content-subscribe .user-content-subscribe-lc .confirm-button {
    margin-right: 10px;
    color: var(--button);
}

.user-content-subscribe .user-content-subscribe-rc .confirm-button {
    top: 0;
    margin-left: 10px;
}

.user-content-subscribe .user-content-subscribe-lc .confirm-button:last-child {
    color: var(--link);
}

.user-content-about > a:hover video {
    display: block;
}

.user-content-about {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.user-content-about > a,
.user-content-about > div {
    width: calc((100% - 70px) / 4);
    font-size: 14px;
}

.user-content-about > div.about-me {
    width: calc((100% - 70px) / 2);
}

.user-content-about .categories-content-tags {
    margin-top: 20px;
}

.user-content h2 {
    margin-top: 20px;
    margin-bottom: 30px;
    padding: 0;
    font-weight: 500;
    font-size: 24px;
}

.user-content-about .user-content-about-titles {
    width: 100%;
    display: flex;
}

.user-content-about .user-content-about-titles h2:nth-child(1) {
    width: calc((100% - 40px) / 3 * 2 + 40px);
}

.user-content .categories-content-videos-video {
    height: 188px;
}

.user-content-about .categories-content-videos-video {
    height: 255px;
}

.user-content .categories-content-albums-album, .user-content .categories-content-models-model {
    height: 340px;
}

.user-content .categories-content-albums, .user-content .categories-content-models, .user-content .categories-content-videos {
    padding-top: 0;
}

.user-content .line {
    margin-bottom: 10px;
    margin-top: 15px;
}

.user-content-about-stats {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: flex-start
}

.user-content-about-stats div {
    width: 50%;
    height: 24px;
}

.user-content-about-stats div:nth-child(2n+1) {
    font-weight: 500;
}

.user-content-about-stats div:nth-child(2n) {
    font-weight: 500;
    text-align: right;
}

.user-content-about-stats div:nth-child(2n) span {
    background: rgba(170, 187, 204, .2);
    padding: 0px 10px;
    display: inline-block;
    border-radius: 10px;
    box-sizing: border-box;
    font-size: 12px;
}