.video-wrapper {
    display: flex;
    flex-direction: row;
}

.video-content {
    flex: 1;
    padding-right: 30px;
    box-sizing: border-box;
    width: calc(100% - 410px);
}

.video-content-video {
    width: 100%;
    height: 580px;
    background-color: var(--primary-dark);
}

.content.content-embed .video-content {
    padding: 0px;
}

.content.content-embed .video-content-video {
    width: 100%;
    height: calc(100vw / 16 * 9);
}

.video-content-video.vertical video {
    width: auto;
    height: 100%;
    margin: 0 auto;
}

.video-content-video video, .video-content-video iframe {
    width: 100%;
    height: 100%;
}

.video-content h1 {
    font-size: 24px;
    font-weight: 500;
    margin-top: 30px;
}

.video-content-description {
    font-size: 14px;
    margin-top: 30px;
}

.video-content-interactions {
    padding-top: 0px;
    padding-bottom: 10px;
    display: flex;
    flex-direction: row;
}

.video-content-interactions-info {
    font-size: 14px;
    display: flex;
    align-items: center;
}

.video-content-interactions-info svg {
    font-size: 18px;
    margin-right: 4px;
}

.video-content-interactions-info svg:not(:first-child){
    margin-left: 15px;
}

.video-content-interactions-buttons {
    display: flex;
    flex-direction: row;
    justify-content: right;
}

.video-content-interactions-buttons .confirm-button.plainOutlined {
    color: var(--button);
    margin-left: 10px;
}

.video-content-interactions-buttons .confirm-button.contained {
    margin-left: 10px;
    top: 0;
}

.video-content-user, .video-content-models, .video-content-categories {
    display: flex;
    flex-direction: row;
    padding-top: 0px;
    padding-bottom: 10px;
    align-items: center;
    flex-wrap: wrap;
}

.video-content-user,
.video-content-model-avatar,
.video-content-model-name,
.album-content-user,
.album-content-model-avatar,
.album-content-model-name  
{
    cursor: pointer;
}

.video-content-user-avatar {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
    background-color: rgb(50, 66, 83);
}

.video-content-user .confirm-button {
    top: 0;
}

.video-content-user-name {
    flex: 1;
    padding-left: 15px;
    font-weight: 700;
}

.video-content-models h2, .video-content-categories h2 {
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    padding: 0;
    margin-bottom: 10px;
    width: 100%;
}

.video-content-model-avatar {
    width: 34px;
    height: 34px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
    margin-right: 10px;
    background-color: rgb(50, 66, 83);
}

.video-content-model-name {
    margin-right: 20px;
}

.video-content .show-more {
    position: relative;
    top: -5px;
}

.tube-player-ad-4 {
    width: 100%;
    height: 250px;
    margin-bottom: 10px;
    overflow: hidden;
}

.video-share-dialog .tabs-component > a {
    padding-top: 0px !important;
}

.video-share-dialog .MuiPaper-root {
    width: 500px;
}

@media (max-width: 1199px) {
    .video-content-video {
       height: calc((100vw - 410px) / 16 * 9);
    }
    .video-content {
        padding-left: 30px;
    }
}