.dialog-form-buttons-stack {
    margin-top: -56px;
    right: 23px;
    border-top: 1px solid var(--link);
    width: 100%;
    justify-content: flex-end;
    padding-top: 10px !important;
}

.dialog-form-buttons-stack label {
    float: right
}

.max-1000 .MuiPaper-root {
    max-width: 1000px;
}

.MuiDialogContent-root {
    overflow: visible !important;
}

.MuiDialogContent-root .form-submit {
    position: relative;
    top: -7px;
}

.MuiDialog-paper {
    border-radius: 20px !important;
    background: var(--primary-light) !important;
    overflow: auto;
}

.MuiDialog-root .MuiPaper-root {
    color: var(--white);
}

.MuiDialog-root .MuiDialogTitle-root {
    color: var(--white);
}

.non-modal-dialog {
    height: auto;
    top: auto !important;
    left: auto !important;
}

.non-modal-dialog .MuiModal-backdrop {
    display: none;
}

.dialog-non-transparent-background .MuiModal-backdrop {
    backdrop-filter: blur(10px);
}

.MuiDialog-root .confirm-button.outlined {
    color: var(--white);
    border-color: var(--white) !important;
}

.MuiDialog-root .confirm-button.outlined:hover {
    color: var(--white);
    border-color: var(--white) !important;
}

.dialog-adult-warning .MuiTypography-root, .dialog-adult-warning .MuiDialogContent-root {
    text-align: center;
}

.dialog-adult-warning .MuiDialogContent-root div {
    justify-content: center;
}