.tube-chip {
    color: var(--black);
    cursor: pointer;
    background-color: var(--link);
    border-radius: 4px;
    align-items: center;
    margin: 2px;
    padding: 2px 12px;
    text-decoration: none;
    transition: all .2s;
    display: flex;
    font-size: 14px;
}

.tube-chip:hover {
    background-color: var(--primary-dark);
    color: var(--white);
}

.tube-chip-temp::after {
    content: "x";
    font-size: 18px;
    font-weight: 300;
    position: relative;
    top: -2px;
    margin-left: 7px;
}