.footer {
    margin-top: 40px;
    border-top: 20px solid var(--primary-dark);
    background-color: var(--primary-dark);
}

.footer-content {
    max-width: 1440px;
    margin: 30px auto;
    margin-bottom: 0px;
    padding-bottom: 20px;
}

.footer-content-navigation {
}

.footer-content-navigation-column {
    width: 280px;
}

.footer-content-navigation-column h3 {
    font-size: 20px;
    font-weight: 600;
    opacity: 0.5;
    margin: 0px;
    padding: 0px;
    margin-bottom: 10px;
}

.footer-content-navigation-column.highlighted h3 {
    color: var(--button);
    opacity: 1;
}


.footer-content-ads,
.footer-content-navigation-links,
.footer-content-navigation-pages {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: center;
}

.footer-content-ads {
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 60px;
}

.footer-content-navigation-backlinks {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: center;
    margin-bottom: 30px;
}

.footer-content-navigation-links .MuiMenuItem-root {
    color: var(--white);
    background-color: transparent !important;
    padding: 0;
    margin-bottom: 4px;
    border-radius: 0;
    transition: padding-left 0.3s;
}

.footer-content-navigation-backlinks .MuiMenuItem-root {
    color: var(--white);
    background-color: var(--primary-light);
    padding: 5px 20px;
    border-radius: 20px;
    padding-left: 10px;
}

.footer-content-navigation-backlinks .MuiMenuItem-root .MuiSvgIcon-root {
    margin-right: 5px;
    color: var(--button);
}

.footer-content-navigation-column .navigation-link.navigation-link-plain {
    color: var(--link);
    background-color: transparent !important;
    padding: 0;
    border-radius: 0;
}

.footer-content-navigation-logo {
    flex: 1;
    text-align: right;
}

.footer-content-navigation-logo img {
    width: 140px;
    height: 22px;
}

.footer-content-copyright-social a {
    width: 36px !important;
    height: 36px !important;
    margin-left: 5px;
}

.footer-content-copyright-social a:hover g:nth-child(2) {
    fill: var(--primary-dark) !important;
}

.footer-content-copyright {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: var(--link);
    font-size: 14px;
    margin-top: 20px;
}

.footer-content-copyright .navigation-link.navigation-link-plain {
    color: var(--link);
    background-color: transparent !important;
    padding: 0;
    margin-right: 8px;
    border-radius: 0;    
}

.footer-content-copyright .navigation-link.navigation-link-plain:hover {
    color: var(--white);
}

.footer-content-copyright-social {
    flex: 1;
    text-align: right;
}

.footer-content-copyright .line {
    margin-top: 15px;
    margin-bottom: 15px;
}

.footer-content .flex-right {
    width: 100%;
    text-align: right;
}

.footer-content-description {
    margin-bottom: 40px;
    width: 100%;
    padding: 0 200px;
    text-align: center;
    box-sizing: border-box;
}

.footer-content-ad {
    background-color: var(--primary-light);
    width: 300px;
    height: 250px;
}