.search-content {
    width: 100%;
    box-sizing: border-box;
}

.search-content h2 {
    font-size: 20px;
    font-weight: 500;
    margin: 0;
}

.search-content .categories-content-videos > a {
    width: calc((100% - 72px) / 5);
}

.search-content .categories-content-videos > a:nth-child(4n+1) {
    margin-left: 18px;
}

.search-content .categories-content-videos > a:nth-child(5n+1) {
    margin-left: 0px;
}

.search-content .categories-content-albums > a,
.search-content .categories-content-models > a {
    width: calc((100% - 108px) / 7);
}

.search-content .navigation-link-total {
    position: relative;
    margin-left: 5px;
    top: -2px;
    line-height: 20px;
}

.search-content .categories-content-models {
    margin-bottom: 50px;
}

.search-content .categories-content-videos-video.show-more,
.search-content .categories-content-albums-album.show-more {
    background-color: rgba(170, 187, 204, .2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.search-content .categories-content-videos-video.show-more div:first-child,
.search-content .categories-content-albums-album.show-more div:first-child {
    font-size: 14px;
    color: "var(--white)";
    margin-bottom: 10px;
}

.search-content .categories-content-videos-video.show-more > div:first-child > span,
.search-content .categories-content-albums-album.show-more > div:first-child > span {
    position: relative;
    left: 3px;
    top: 0px;
}

.search-content .categories-content-albums-album.show-more {
    margin-left: 18px;
}

.search-content .confirm-button.plainOutlined {
    color: var(--button);
}

.search-content .search-channel, .search-content .categories-content-channels-channel {
    height: 160px;
}

.search-content .categories-content-videos > .ad-wrapper {
    width: calc((100% - 72px) / 5);
}