.categories-sidebar .navigation-link {
    color: var(--white);
    background-color: var(--primary-dark) !important;
    padding: 8px 16px;
    margin-bottom: 4px;
    border-radius: 4px;
}

.categories-sidebar .navigation-link.navigation-link-plain {
    color: var(--link);
    background-color: transparent !important;
    padding: 0;
    margin-bottom: 4px;
    border-radius: 0;
    transition: padding-left 0.3s;
}

.categories-sidebar .navigation-link.navigation-link-plain:hover {
    padding-left: 8px;
}

.categories-sidebar .confirm-button.plainOutlined {
    margin-top: 10px;
}

.categories-sidebar {
    width: 230px;
}

.categories-wrapper h2 {
    font-size: 16px;
    font-weight: 600;
    margin-top: 0px;
}

.categories-sidebar .categories-sidebar-channel {
    background-color: var(--primary-dark);
    height: 44px;
    border-radius: 4px;
    width: 100%;
    background-size: cover;
    background-position: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 4px;
    box-sizing: border-box;
}

.categories-sidebar .categories-sidebar-channel-name {
    color: var(--white);
    text-decoration: none;
    margin-top: 4px;
    margin-bottom: 8px;
    font-size: 14px;
}

.categories-sidebar .categories-sidebar-channel-user {
    width: 36px;
    height: 36px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
}

.categories-sidebar  .categories-sidebar-channel-button-wrapper .confirm-button {
    font-size: 26px;
    top: 0;
}

.categories-sidebar  .categories-sidebar-channel-button-wrapper {
    flex: 1;
    display: flex;
    justify-content: right;
}

.categories-sidebar .categories-sidebar-profiles {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

.categories-sidebar .categories-sidebar-profiles a {
    width: 100%;
    display: flex;
    transition: padding-left 0.3s;
    margin-top: 5px;
}

.categories-sidebar .categories-sidebar-profiles a:hover {
    padding-left: 8px;
}

.categories-sidebar .categories-sidebar-profile {
    background-color: var(--primary-dark);
    height: 34px;
    border-radius: 100%;
    width: 34px;
    background-size: cover;
    background-position: 50%;
}

.categories-sidebar .categories-sidebar-profile-name {
    color: var(--white);
    text-decoration: none;
    font-size: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-top: -3px;
    margin-left: 10px;
}

.categories-wrapper, .home-wrapper {
    display: flex;
    flex-direction: row;
}

.categories-content, .home-content {
    max-width: 1210px;
    width: calc(100% - 230px);
    box-sizing: border-box;
}

.categories-wrapper h1, .home-wrapper h1 {
    font-size: 24px;
    font-weight: 500;
    margin: 0
}

.categories-content .categories-content-header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.categories-content .categories-content-header .confirm-button {
    margin-left: 20px;
    color: var(--button);
}

.categories-content, .home-content {
    padding-left: 30px;
    flex: 1
}

.categories-content-description {
    font-size: 14px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.categories-content-tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.categories-content-videos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 5px;
}

.categories-content-videos-user-wrapper {
    display: flex;
    flex-direction: row;
}

.categories-content-videos > a,
.categories-content-videos > .ad-wrapper {
    width: calc((100% - 54px) / 4);
    display: flex;
    flex-direction: column;
    margin-bottom: 18px;
    margin-left: 18px;
}

.categories-content-videos > a:nth-child(4n+1) {
    margin-left: 0;
}

.categories-content-videos > a:hover video {
    display: block;
}

.categories-content-videos-video:hover video {
    display: block;
}

.categories-content-videos-video {
    width: 100%;
    height: 160px;
    background-size: cover;
    background-position: 50%;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
    background-color: var(--primary-dark);
    display: flex;
    align-items: center;
    justify-content: center;
}

.categories-content-videos-video .no-preview-text {
    font-size: 14px;
    font-weight: 600;
    color: var(--link);
}

.categories-content-videos-video video {
    display: none;
    width: 100%;
    height: 100%;
    object-fit: fill;
}
 
.categories-content-videos-user {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
    background-color: var(--link);
    border: 2px solid var(--primary);
    margin-top: -12px;
    margin-left: 5px;
    position: relative;
}
 
.categories-content-videos-username {
    color: var(--white);
    opacity: .5;
    font-size: 12px;
    margin-left: 8px;
    margin-top: 2px;
}
 
.categories-content-videos-name {
    color: var(--white);
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    margin-top: 5px;
}

.categories-content-videos-video-length {
    position: absolute;
    bottom: 5px;
    right: 5px;
    color: var(--white);
    font-size: 12px;
    background-color: var(--primary-dark);
    padding: 0 6px
}

.categorylist-pagination a {
    color: var(--link);
    font-weight: 400;
    padding-top: 30px;
    display: inline-block;
    padding-bottom: 15px;
    padding-left: 8px;
    padding-right: 8px;
}

.categorylist-pagination a.active {
    color: var(--button);
    border-bottom: 2px solid var(--button);
}

.categorylist-pagination a.inactive {
    opacity: .15;
    cursor: text;
}

.categorylist-pagination {
    width: 100%;
    border-bottom: 1px solid var(--link);
}

.categorylist-content {
    width: 100%;
}

.categorylist-content-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.categorylist-content-category {
    height: 148px;
    width: calc((100% - 72px) / 5);
    margin-left: 18px;
    margin-top: 18px;
    background-color: var(--primary-dark);
    display: flex;
    align-items: flex-end;
    position: relative;
    overflow: hidden;
}

.categorylist-category-thumbnail {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: 50%;
    left: 0;
    top: 0;
    transition: width 0.3s, height 0.3s, left 0.3s, top 0.3s;
}

.categorylist-content-category:hover .categorylist-category-thumbnail {
    width: 110%;
    height: 110%;
    left: -5%;
    top: -5%;
}

.categorylist-content-category:nth-child(5n+1) {
    margin-left: 0px;
}

.categorylist-content-category span {
    width: 100%;
    font-size: 22px;
    font-weight: 500;
    padding: 15px;
    color: var(--white);
    background-image: linear-gradient(to top, rgba(0, 17, 34, .8) 0%, rgba(17, 34, 51, 0) 100%);
    position: relative;
}

.tube-player-ad-6 {
    width: 100%;
    height: 120px;
}