.not-found-page {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.not-found-background {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100vw;
    height: 100%;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
    opacity: 0.15;
  }

  .not-found-page .error-heading {
    font-size: 120px;
    line-height: 160px;
    padding-top: 80px;
    text-align: center;
    font-weight: 600;
    color: var(--button);
  }

  .not-found-page .error-message {
    font-size: 24px;
    margin-top: -20px;
    text-align: center;
    color: var(--white);
    font-weight: 600;
  }

  .not-found-page .confirm-button {
    margin-top: 30px;
  }

  .not-found-page .MuiTypography-root {
    margin-top: 40px;
  }

  .not-found-page .video-list {
    display: flex;
    direction: row;
    gap: 20px;
    width: 1200px;
    margin-top: 30px;
    margin-bottom: 200px;
  }

  .not-found-page .video-list a {
    width: calc((100% - 60px) / 4);
  }