.navigation {
    display: flex;
    flex-direction: row;
    flex: 1;
}

.navigation-block {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1440px;
}

/* .navigation-block > div {
    display: flex;
    flex-direction: row;
    align-items: center;
} */

.navigation-block > div > .confirm-button {
    top: 0;
}

.navigation .MuiMenuItem-root {
    background-color: transparent !important;
    color: var(--white) !important
}

.navigation-wrapper {
    background-color: var(--primary-dark) !important;
    display: flex;
    justify-content: center;
}

.navigation-link-total {
    display: inline-block;
    background-color: var(--primary-dark);
    border-radius: 20px;
    margin-right: 4px;
    margin-left: 8px;
    padding-left: 9px;
    padding-right: 9px;
    font-size: 11px;
}

.navigaion-link-dropdown-icon {
    margin-top: 3px;
}