.tags-sidebar .navigation-link {
    color: var(--white);
    background-color: var(--primary-dark) !important;
    padding: 8px 16px;
    margin-bottom: 4px;
    border-radius: 4px;
}

.tags-sidebar .navigation-link.navigation-link-plain {
    color: var(--link);
    background-color: transparent !important;
    padding: 0;
    margin-bottom: 4px;
    border-radius: 0;
    transition: padding-left 0.3s;
}

.tags-sidebar .navigation-link.navigation-link-plain:hover {
    padding-left: 8px;
}

.tags-sidebar .confirm-button.plainOutlined {
    margin-top: 10px;
}

.tags-sidebar {
    width: 230px;
}

.tags-wrapper h2 {
    font-size: 16px;
    font-weight: 600;
    margin-top: 0px;
}

.tags-sidebar .tags-sidebar-channel {
    background-color: var(--primary-dark);
    height: 44px;
    border-radius: 4px;
    width: 100%;
    background-size: cover;
    background-position: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 4px;
    box-sizing: border-box;
}

.tags-sidebar .tags-sidebar-channel-name {
    color: var(--white);
    text-decoration: none;
    margin-top: 4px;
    margin-bottom: 8px;
    font-size: 14px;
}

.tags-sidebar .tags-sidebar-channel-user {
    width: 36px;
    height: 36px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
}

.tags-sidebar  .tags-sidebar-channel-button-wrapper .confirm-button {
    font-size: 26px;
    top: 0;
}

.tags-sidebar  .tags-sidebar-channel-button-wrapper {
    flex: 1;
    display: flex;
    justify-content: right;
}

.tags-sidebar .tags-sidebar-profiles {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

.tags-sidebar .tags-sidebar-profiles a {
    width: calc(50% - 4px);
}

.tags-sidebar .tags-sidebar-profile {
    background-color: var(--primary-dark);
    height: 60px;
    border-radius: 4px;
    width: 100%;
    background-size: cover;
    background-position: 50%;
}

.tags-sidebar .tags-sidebar-profile-name {
    color: var(--white);
    text-decoration: none;
    margin-top: 4px;
    margin-bottom: 8px;
    font-size: 14px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.tags-wrapper, .home-wrapper {
    display: flex;
    flex-direction: row;
}

.tags-content, .home-content {
    max-width: 1210px;
    width: calc(100% - 230px);
    box-sizing: border-box;
}

.tags-wrapper h1, .home-wrapper h1 {
    font-size: 24px;
    font-weight: 500;
    margin: 0
}

.tags-content .tags-content-header {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tags-content .tags-content-header .confirm-button {
    margin-left: 20px;
    color: var(--button);
}

.tags-content, .home-content {
    padding-left: 30px;
    flex: 1
}

.tags-content-description {
    font-size: 14px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.tags-content-tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.tags-content-videos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 5px;
}

.tags-content-videos-user-wrapper {
    display: flex;
    flex-direction: row;
}

.tags-content-videos > a {
    width: calc((100% - 54px) / 4);
    display: flex;
    flex-direction: column;
    margin-bottom: 18px;
    margin-left: 18px;
}

.tags-content-videos > a:nth-child(4n+1) {
    margin-left: 0;
}

.tags-content-videos > a:hover video {
    display: block;
}

.tags-content-videos-video {
    width: 100%;
    height: 160px;
    background-size: cover;
    background-position: 50%;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
}

.tags-content-videos-video video {
    display: none;
    width: 100%;
    height: 100%;
    object-fit: fill;
}
 
.tags-content-videos-user {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-size: cover;
    background-position: 50%;
    background-color: var(--link);
    border: 2px solid var(--primary);
    margin-top: -12px;
    margin-left: 5px;
    position: relative;
}
 
.tags-content-videos-username {
    color: var(--white);
    opacity: .5;
    font-size: 12px;
    margin-left: 8px;
    margin-top: 2px;
}
 
.tags-content-videos-name {
    color: var(--white);
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    margin-top: 5px;
}

.tags-content-videos-video-length {
    position: absolute;
    bottom: 5px;
    right: 5px;
    color: var(--white);
    font-size: 12px;
    background-color: rgba(0,0,0,0.6);
    padding: 0 6px
}

.taglist-pagination a {
    color: var(--link);
    font-weight: 400;
    padding-top: 30px;
    display: inline-block;
    padding-bottom: 15px;
    padding-left: 8px;
    padding-right: 8px;
}

.taglist-pagination a.active {
    color: var(--button);
    border-bottom: 2px solid var(--button);
}

.taglist-pagination a.inactive {
    opacity: .15;
    cursor: text;
}

.taglist-pagination {
    width: 100%;
    border-bottom: 1px solid var(--link);
}

.taglist-content {
    width: 100%;
}

.taglist-content-list {
    display: flex;
    margin: 20px 0;
    flex-direction: row;
    flex-wrap: wrap;
}

.taglist-content-tag {
    width: calc((100% - 261px) / 5);
    margin-left: 60px;
    padding: 5px 10px;
    box-sizing: border-box;
    display: flex;
    align-items: flex-end;
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    border-radius: 4px;
}

.taglist-content-tag:hover {
    background-color: rgba(255,255,255,0.05);
}

.taglist-tag-thumbnail {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: 50%;
    left: 0;
    top: 0;
    transition: width 0.3s, height 0.3s, left 0.3s, top 0.3s;
}

.taglist-content-tag:hover .taglist-tag-thumbnail {
    width: 110%;
    height: 110%;
    left: -5%;
    top: -5%;
}

.taglist-content-tag:nth-child(5n+2) {
    margin-left: 0px;
}

.taglist-content-tag span {
    width: 100%;
    font-size: 14px;
    font-weight: 500;
    color: var(--white);
    position: relative;
}

.taglist-content-tag .tag-total-videos {
    background-color: rgba(170, 187, 204, .2);
    border-radius: 20px;
    margin-right: 4px;
    padding-left: 9px;
    padding-right: 9px;
    font-size: 11px;
    font-weight: 300;
    color: var(--white);
}

.taglist-content .text-input {
    margin-bottom: 20px;
}